          align-items: center;
}
.NavBar.desktop .Nav {
  display: -webkit-flex;
  display: flex;
  height: 3rem;
  -webkit-align-items: center;
          align-items: center;
}
.Nav p {
  margin: 0 var(--margin);
  color: white;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  pointer-events: all;
}
.Nav p:hover {
