}
.NavBar.mobile {
  display: none;
}
.NavOrderMobile {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
#mobileMenu {
  width: 0;
}
.changeMobileMenuBtn {
  color: white;
  font-size: 1.8em;
  padding: 0.2em 0.5em;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  position: relative;
}
.MobileMenuContainer {
  position: absolute;
  top: 2.9em;
  right: 0;
  width: 10em;
  background-color: var(--color1);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  opacity: 0;
  pointer-events: none;
  min-height: 100vh;
}
.MobileMenuContent {
  position: relative;
  color: var(--color1);
  font-size: 2em;
}
.MobileMenuContent .SocialLinks {
  margin: 0;
  font-size: 1.5em;
  margin-top: 1em;
}
.NavOrderMobile input:checked + .changeMobileMenuBtn {
  color: var(--color1);
  background-color: var(--color2);
}

.NavOrderMobile input:checked ~ .MobileMenuContainer {
  /* display: inline; */
  opacity: 1;
  pointer-events: all;
  width: 20em;
  max-width: 100%;
}
@media (max-width: 40rem) {
  .NavBar.desktop {
    opacity: 0.5;
    display: none;
  }
  .NavBar.mobile {
    display: inline;
  }
}

